.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}

.App {
  font-family: "Open Sans", Arial, Helvetica, sans-serif;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

h1 {
  color: #128AF7;
  font-weight: 100;
  letter-spacing: 1px;
  text-align: left;
  margin-top: 0;
  position: absolute;
  left: 1rem;
  top: 1rem;
}

h2 {
  font-weight: 600;
  margin-bottom: 3rem;
}

.grid line {
  stroke: #BDBDC1;
  stroke-opacity: 0.7;
  shape-rendering: crispEdges;
}

.grid path {
  stroke-width: 0;
}

.crypto-logo-container {
  max-width: 1000px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.crypto-logo {
  width: auto;
  height: 75px;
}
.crypto-logo:not(:last-child) {
  margin-right: 50px;
}
.crypto-logo:hover {
  -webkit-box-shadow: 0 0 10px 10px white;
          box-shadow: 0 0 10px 10px white;
}
.axis, .domain {
  stroke: #BDBDC1;
}
.axis--y .domain {
  stroke: none;
}
.axis--y .tick line {
  stroke: none;
}
.tick text {
  fill: #BDBDC1;
}
.tick line {
  stroke: #BDBDC1;
}
.line {
  -webkit-clip-path: url(#clip);
          clip-path: url(#clip);
}
.dot {
  cursor: pointer
}
.dot--green {
  fill: rgba(0,255,0,.5);
}
.dot--purple {
  fill: rgba(255,0,255,.5);
}
.dot--red {
  fill: rgba(255,0,0,.5);
}
.zoom {
  cursor: move;
  fill: none;
  pointer-events: all;
}

.navbar {
  font-family: 'OCR', Arial;
  background-color: #000000;
  color: #FFFFFF;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
  -ms-flex-direction: column;
      flex-direction: column;
}

.navbar__item {
  list-style: none;
  -webkit-transition: background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              color 0.55s ease;
  -o-transition: background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              color 0.55s ease;
  transition: background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              color 0.55s ease;
}

.navbar__item:first-child {
  border-top: 1px solid #FFFFFF;
}

.navbar__item:last-child {
  border-bottom: 1px solid #FFFFFF;
}

.navbar__item:hover {
  color: #000000;
  background-color: #FFFFFF;
}

.navbar__item:hover .navbar__link {
  color: #000000;
}

.navbar__link {
  display: block;
  color: #FFFFFF;
  padding: 1rem;
  text-decoration: none;
}

.navbar__menu {
  border-bottom: 1px solid #FFFFFF;
}

.navbar__menu span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #cdcdcd;
  border-radius: 3px;  
  z-index: 1;
}

.section-container {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.section-container--light {
  color: #000000;
  background: #FFFFFF;
}

.section-container--dark {
  color: #FFFFFF;
  background: -webkit-radial-gradient(#232745, #1B2034);
  background: -o-radial-gradient(#232745, #1B2034);
  background: radial-gradient(#232745, #1B2034);
}

.section {
  line-height: 2;
  max-width: 1000px;
  text-align: left;
  font-weight: 100;
  margin: 1rem;
}

.section--centered {
  text-align: center;
}

.section--viz {
  background: #141C33;
  padding: 3rem;
}


